body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo-icon {
  padding: 10px 4px 4px 14px;
}
.logo-icon img {
  max-width: 50px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  display: flex;
  height: 55px;
  box-shadow: rgb(234 229 229 / 35%) 0px 5px 8px;
  background-color: #f5f2f29e;
  align-items: center;
}

.shop-title {
  font-size: 24px;
  font-weight: 700px;
  padding: 8px 24px;
  flex-grow: 1;
}

.header .search {
  padding-right: 16px;
}

.header .search input {
  width: 200px;
  border-radius: 12px;
  border: 1px solid #ffff;
  outline: none;
  padding: 5px;
  transition: width 0.7s;
}

.header .search input:focus {
  width: 350px;
}

.header .cart {
  position: relative;
}

.header .cart .cart-icon{
  padding-right: 30px;
  cursor: pointer;
}

.header .cart .number-items {
  position: absolute;
  top: -8px;
  right: 10px;
  background: #d41010;
  padding: 1px 3px;
  border-radius: 50%;
  font-size: 0.7rem;
  color: #fff;
  width: 18px;
  text-align: center;
}
.cart-header {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #efebeb;
  padding-bottom: 12px;
}

.cart-header .cart-title {
  flex: 1;
  font-size: 24px;
}
.cart-header .cart-close {
  text-align: right;
  cursor: pointer;
}

.cart-header .cart-close span {
  padding: 2px 6px;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  border-radius: 4px;
}

.cart-body {
  border-bottom: 1px solid #efebeb;
  padding-bottom: 12px;
}

.cart-body .empty-cart {
  min-height: 200px;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 700;
}
.cart-item {
  display: flex;
  align-items: center;
  margin: 40px 0px;
  justify-content: space-evenly;
}

.cart-item .cart-item-thumb {
  width: 20%;
}

.cart-item .cart-item-description {
  padding: 0px 8px;
  width: 60%;
  display: flex;
  flex-direction: column;
}

.cart-item-description .cart-item-name {
  font-size: 14px;
  font-weight: 700;
}

.cart-item-description .cart-item-price {
  font-size: 12px;
  font-weight: 600;
  color: #867f7f;
}

.cart-item-description .cart-item-price::before {
  content: 'â‚¬';
  padding-right: 3px;
}

.cart-item-description .cart-item-remove {
  font-size: 12px;
  color: #c45252;
  cursor: pointer;
}

.cart-item-description .cart-item-remove:hover {
  color: red;
}

.cart-item .cart-item-counter {
  width: 30%;
}

.cart-item .cart-item-thumb img{
  max-width: 65px;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  border-radius: 5px;
}

.cart-counter {
  display: flex;
  align-items: center;
  justify-content: center
}

.cart-counter .cart-counter-dec,.cart-counter-inc {
  background-color: #343a40;
  border-radius: 50%;
  color: #fff;
  font-weight: bold;
  margin: 0px 12px;
  width: 30px;
  height: 27px;
  font-size: 18px;
  line-height: 18px;
}

.cart-counter .cart-counter-val {
  font-size: 1rem;
  font-weight: 700;
  min-width: 10px;
  text-align: center;
}

/*
  Items
*/

.items-container {
  display: flex;
  flex-wrap: wrap;
  padding: 30px;
}

.items-container .item {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 12px;
  margin: 8px;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  border-radius: 5px;
}

.items-container .item:hover {
  box-shadow: rgb(90 160 181 / 35%) 0px 5px 15px;
}

.items-container .item .item-image img {
  max-width: 300px;
}

.item-name {
  font-size: 13px;
  font-weight: 500;
  color: #867f7f;
  padding: 4px;
}

.item-price {
  font-size: 14px;
  font-weight: 700;
  color: #3a3333;
  padding: 8px;
}

.item-category {
  cursor: pointer;
  display: flex;
  flex-direction: row-reverse;
  font-size: 10px;
}

/*
Item Detail
*/
.item-detail {
  position: relative;
}
.item-detail .item-detail-close {
  cursor: pointer;
  position: absolute;
  top: 8px;
  right: 0px;
  padding: 1px 6px;
  box-shadow: rgb(0 0 0 / 35%) 0px 5px 15px;
  border-radius: 8px;
}

.item-detail .item-name h1 {
  font-size: 25px;
  letter-spacing: 1px;
}

.item-detail .item-price {
  font-size: 18px;
  font-weight: 700;
  color: #5f4f4f;
  padding: 8px;
  letter-spacing: 2px;
}

.item-detail .item-image img {
  width: 100%;
  box-shadow: rgb(90 160 181 / 35%) 0px 5px 15px;
  margin: 16px 0px;
}

.item-detail .add-to-cart {
  width: 100%;
  position: fixed;
  bottom: 0;
}

.item-detail .add-to-cart button {
  border-radius: 12px;
  background: #0fb86a;
  color: #fff;
  font-weight: 700;
  border: 0;
  padding: 14px 30px;
  font-size: 15px;
  cursor: pointer;
  width: 350px;
  text-align: center;
  margin: 10px;
}
.item-detail .add-to-cart button .cart-button svg {
  fill: #fff;
}
.item-detail .add-to-cart button .add-to-cart-text {
  padding: 4px 12px;
}

.item-detail .add-to-cart button:hover {
  box-shadow: rgb(30 153 190 / 91%) 0px 5px 15px;
}

.quantity .add-remove-btn {
  padding: 8px 24px;
}


.item-counter {
  display: flex;
  align-items: center;
  justify-content: center
}

.item-counter .item-counter-dec,.item-counter-inc {
  background-color: #343a40;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #fff;
  font-weight: bold;
  font-size: 30px;
  line-height: 30px;
  margin: 0px 12px;
}

.item-counter .item-counter-val {
  font-size: 1.5rem;
  font-weight: 700;
  min-width: 40px;
  text-align: center;
}

.sub-total {
  display: flex;
  margin-top: 20px;
  align-items: center;
}
.sub-total .sub-total-title {
  font-size: 1.2rem;
}

.sub-total .sub-total-price {
  font-size: 1.4rem;
  font-weight: 700;
  padding-left: 40px;
}

.cart-footer {
  position: fixed;
  bottom: 0;
}

.cart-footer button {
  border-radius: 12px;
  background: #0f6cb8;
  color: #fff;
  font-weight: 700;
  border: 0;
  padding: 14px 30px;
  font-size: 15px;
  cursor: pointer;
  width: 350px;
  text-align: center;
  margin: 10px;
}

.cart-footer button .cart-button svg {
  fill: #fff;
}
.cart-footer button .add-to-cart-text {
  padding: 4px 12px;
}

.cart-footer button:hover {
  box-shadow: rgb(30 153 190 / 91%) 0px 5px 15px;
}